import firebase from "./services/firebase";
import React, { Component, useState } from "react";
import Slider, { Range } from 'rc-slider';
import 'rc-slider/assets/index.css';

import 'bootstrap/dist/css/bootstrap.min.css';
import {
  Button,
  Container,
  Row,
  Col
} from "react-bootstrap";

import dateFormat from 'dateformat';


const db = firebase.collection("/BGU-lab-dev");

class HappinesPage extends Component{

  constructor(props) {
    super(props);
    this.state ={
      testCouner: 0,
      sliderValue: 0,
      start:false,
      lastDocID:"",
      question: "happines",

    }
    this.handleChange = this.handleChange.bind(this);
    this.handleSubmit = this.handleSubmit.bind(this);
    this.handleReset = this.handleReset.bind(this);
    this.start = this.start.bind(this);


    

  }
  componentDidMount() {
    this.unsubscribe = firebase.collection('/slider-data').onSnapshot(this.onCollectionUpdate) 
    this.setState({sliderValue:0})

  }

  componentWillUnmount() {
    // we have to unsubscribe when component unmounts, because we don't need to check for updates
    this.unsubscribe()
  }

  
  onCollectionUpdate = (querySnapshot) => {
    // we have to update the state
    let value = 0;
    querySnapshot.forEach((document) => {
      const doc= document.data();
      console.log(doc)
      value = doc.value
    })
    this.setState({value:value, sliderValue:value})

  }



  getData() {
    let value;
    db.get().then(function(snapshot) {
      snapshot.forEach( function(childSnapshot){
        db.doc(childSnapshot.id).get().
        then( docSnapshot => {
          [docSnapshot].forEach( doc => {
            const data = doc.data();
            console.log(doc.id, data);
            console.log(`sliderID: ${data.id}, value: ${data.value}`)
            value =  data.value;
          })

        })
        //console.log(`id: ${childSnapshot.id}, data: ${childSnapshot.data}`)
      })
    })
    console.log(value)
    return value
  }



  create(tutorial) {
    return db.add(tutorial);
  }



  update( input) {
    db.get().then(function(snapshot) {
      snapshot.forEach( function(childSnapshot){
        db.doc(childSnapshot.id).update({value:input})

      })
    })
  }

  addTestScoreToDoc2(input){
         db.orderBy('time', 'desc').limit(1).get().
            then((data) =>{
              const docRef = data.docs[0];
              const docData = docRef.data()
              const docID = docRef.id
              let currTestCouner = this.state.testCouner
              if(this.state.lastDocID !== docRef.id) {
                this.setState({lastDocID: docRef.id, testCouner:1})
                currTestCouner=1;
              }

              let dataToUpdate = docData["calibrationTests"]

              this.setState(prevState =>  {return {testCouner: prevState.testCouner+1} })
              let time = dateFormat("dd-mm-yyyy HH:MM:ss")
              console.log('time: ' +time)
              let question = (this.state.question === "happines")? "אושר" :
              "מתח"
              
              let newData ={testNum : ` ${question} ${currTestCouner}`, value:input, time:time }
              //newData[scoreCounter] = input
              dataToUpdate.push(newData)
              const docRefUpdate = db.doc(docRef.id);
              docRefUpdate.update({calibrationTests: dataToUpdate})
              if (this.state.question ==="happines"){
                this.setState({question:"stress"})
              }
          


            } 
            )
  }


 
    
  handleChange(event) {
    this.setState({input: event.target.value}, ()=>{
        this.update(event.target.value,this)
      });
    }

  handleSubmit(event) {
    event.preventDefault();
    //this.update(this.state.sliderValue)
    this.addTestScoreToDoc2(this.state.sliderValue)
    this.setState({sliderValue:0})


  }

  handleReset(event) {
    event.preventDefault();
    //this.setState({sliderValue:0})
    this.setState({sliderValue:0})
    

  }

  start(event) {
    event.preventDefault();
    //this.setState({sliderValue:0})
    this.setState({sliderValue:0, start:true})

  }

  delete(id) {
    return db.doc(id).delete();
  }

  onSliderChange = value => {
    this.setState(
      {
        sliderValue:value
      },
      () => {
        console.log(this.state.sliderValue);
      }
    );
  };

  
  render(){
    let sliderHeight = 200;
    return (
        <Row>

          <Col>
            {
            this.state.question === "happines" ?
            <h3>?ברגע זה, כמה אתה מרגיש מאושר</h3>
            :

            <h3>?ברגע זה, כמה אתה מרגיש מתוח</h3>

            }

        <Row>

          <Col >
          {!this.state.start ?  
                    <Button onClick ={this.start}>
                    התחל
                  </Button>
          :
                <div>
                     
                       <Button
                variant="outline-primary"
                onClick={this.handleSubmit}
              >
                  Submit
              </Button>

              <Row>
                    <Col xs={1} >
                    <h6 style={{margin:"0px"}}> הכי הרבה</h6>
                    </Col>

                    <Col xs={10}>

                    </Col>
                    
                    <Col xs={1} style={{marginRight:"0px", paddingLeft:"0px"}}>
                      <h6 >בכלל לא</h6>
                    </Col>
              </Row>
              

              <Slider
              min={0}
              max={10}
              defaultValue={8}
              value={this.state.sliderValue}
              onChange={this.onSliderChange}
              step ={0.1}
              reverse = {true}
              railStyle={{ backgroundColor: 'grey', height: sliderHeight }}
              trackStyle={{ backgroundColor: 'red', height: sliderHeight }}
              handleStyle={{
                borderColor: 'grey',
                height: sliderHeight,
                width: 1,
                marginLeft: -14,
                marginTop: 0,
                backgroundColor: 'white',
              }}

              />

              {/* slider value: {this.state.sliderValue} */}


                  
                </div>
          }

     
           
          </Col>
        </Row>
    


        </Col>
        </Row>

    )
  }
}

export default HappinesPage;