import firebase from "./services/firebase";
import React, { Component, useState } from "react";
import Slider, { Range } from 'rc-slider';
import 'rc-slider/assets/index.css';

import 'bootstrap/dist/css/bootstrap.min.css';
import {
  Button,
  Container,
  Row,
  Col
} from "react-bootstrap";


const db = firebase.collection("/BGU-lab-dev");

class InteractionPageOn extends Component{

  constructor(props) {
    super(props);
    this.state ={
      sliderValue: 0,
      saved:false,
      start:false,
      q2:"",
      selectedOption:"",
      bestSpot:"",
      selectedOption2:"",
      q5:"",
      q6:"",
      q7:"",

    }

    this.start = this.start.bind(this);
    this.handleChange = this.handleChange.bind(this);
    this.handleSubmit = this.handleSubmit.bind(this);
    this.handleOptionChange = this.handleOptionChange.bind(this);
    this.handleOptionChange2 = this.handleOptionChange2.bind(this);
    this.handleOptionChange3 = this.handleOptionChange3.bind(this);
    this.handleOptionChange4 = this.handleOptionChange4.bind(this);





    

  }
  componentDidMount() {
    this.unsubscribe = firebase.collection('/slider-data').onSnapshot(this.onCollectionUpdate) 
    this.setState({sliderValue:0})

  }

  componentWillUnmount() {
    // we have to unsubscribe when component unmounts, because we don't need to check for updates
    this.unsubscribe()
  }

  
  onCollectionUpdate = (querySnapshot) => {
    // we have to update the state
    let value = 0;
    querySnapshot.forEach((document) => {
      const doc= document.data();
      console.log(doc)
      value = doc.value
    })
    this.setState({value:value, sliderValue:value})

  }



  getData() {
    let value;
    db.get().then(function(snapshot) {
      snapshot.forEach( function(childSnapshot){
        db.doc(childSnapshot.id).get().
        then( docSnapshot => {
          [docSnapshot].forEach( doc => {
            const data = doc.data();
            console.log(doc.id, data);
            console.log(`sliderID: ${data.id}, value: ${data.value}`)
            value =  data.value;
          })

        })
        //console.log(`id: ${childSnapshot.id}, data: ${childSnapshot.data}`)
      })
    })
    console.log(value)
    return value
  }






  update( input) {
    db.get().then(function(snapshot) {
      snapshot.forEach( function(childSnapshot){
        db.doc(childSnapshot.id).update({value:input})

      })
    })
  }




 
    


  onSliderChange = value => {
    this.setState(
      {
        sliderValue:value
      },
      () => {
        console.log(this.state.sliderValue);
      }
    );
  };


  
  handleChange(event) {
    const value = event.target.value;
    const name = event.target.name
    let update =  {}
    update[name] = value
    this.setState(update);
  }

  async handleSubmit(event) {
    event.preventDefault();
    db.orderBy('time', 'desc').limit(1).get().
            then((data) =>{
              const docRef = data.docs[0];
              const docData = docRef.data()
              const docID = docRef.id
              const dataUpdate = {
                  "כאשר אני מלטף את הגב של פארו מספר פעמים": this.state.q1,
                 "כאשר אני מלטף את הפנים של פארו מספר פעמים":this.state.q2,
                 "כאשר אני קורא לפארו בשמו מספר פעמים הוא": this.state.q3,
                 "אנא סמנו האם צפיתם בפארו מזיז את הסנפירים האחוריים שלו":this.state.selectedOption,
                 "אנא סמנו האם צפיתם בפארו פותח וסוגר את עיניו":this.state.selectedOption2,
                 "אנא סמנו האם צפיתם בפארו עושה קולות":this.state.selectedOption3,
                 "האם אתה חושב שפארו כבד?": this.state.selectedOption4,
                }

              const docRefUpdate = db.doc(docRef.id);
            //   docRefUpdate.update(dataUpdate)
              docRefUpdate.update(dataUpdate)
              console.log('updating doc with: ' +JSON.stringify(dataUpdate) )
              this.setState({saved:true})
            })
 

  }

  
handleOptionChange(e) {
    this.setState({
      selectedOption: e.target.value
    });
  }

  handleOptionChange2(e) {
    this.setState({
      selectedOption2: e.target.value
    });
  }
  handleOptionChange3(e) {
    this.setState({
      selectedOption3: e.target.value
    });
  }
  handleOptionChange4(e) {
    this.setState({
      selectedOption4: e.target.value
    });
  }
  
  start(event) {
    event.preventDefault();
    //this.setState({sliderValue:0})
    this.setState({sliderValue:0, start:true})

  }

  render(){
    let sliderHeight = 200;
    return (
        <Row>

          <Col>
          {
          !this.state.start ?  
                    <Button onClick ={this.start}>
                    התחל
                  </Button>
          :


        <Row>
            <Col>

           
            <Row>
                <Col>
                    <h3>:למקרים הבאים PARO בשאלות הבאת אנא תארו את התגובה של</h3>
                </Col>
            </Row>

        
            
            <form onSubmit={this.handleSubmit}>

      
                <Row style={{height:"50px"}}>
                    <Col>
                    </Col>
                </Row>
   
                <Row>
                    <Col>
                      ...מספר פעמים, הוא
                      PARO
                      כאשר אני מלטף את הגב של
                         <Row>
                            <Col>
                            <textarea style={{width:"50%"}} name = {"q1"} value={this.state.q1} onChange={this.handleChange} />
                            </Col>
                        </Row>
                    </Col>
                </Row>

                <Row style={{height:"50px"}}>
                    <Col>
                    </Col>
                </Row>

                <Row>
                    <Col>
                      ...ספר פעמים, הוא
                      PARO
                      כאשר אני מלטף את הפנים של
                         <Row>
                            <Col>
                            <textarea style={{width:"50%"}} name = {"q2"} value={this.state.q2} onChange={this.handleChange} />
                            </Col>
                        </Row>
                    </Col>
                </Row>

                <Row style={{height:"50px"}}>
                    <Col>
                    </Col>
                </Row>

                <Row>
                    <Col>
                      ...בשמו מספר פעמים, הוא
                      PARO
                      -כאשר אני קורא ל
                         <Row>
                            <Col>
                            <textarea style={{width:"50%"}} name = {"q3"} value={this.state.q3} onChange={this.handleChange} />
                            </Col>
                        </Row>
                    </Col>
                </Row>





                <Row style={{height:"50px"}}>
                    <Col>
                    </Col>
                </Row>

                <Row style={{height:"50px"}}>
                    <Col>
                    :עושה את אחת מהפעולות הבאות
                    PARO
                    אנא סמנו אם צפיתם ב
                    </Col>
                </Row>
   
   

                <Row>
                    <Col>
                    
                  מזיז את הסנפירים האחוריים שלו
                    <Row  className="justify-content-md-center">
                                <Col xs lg="2">
                                <label>
                                <input type='radio' name = {"head"} 
                                    value="כן ראיתי" 
                                    checked={this.state.selectedOption === 'כן ראיתי'} 
                                    onChange={this.handleOptionChange} />
                                    כן ראיתי
                                </label>
                                
                                </Col>

                                <Col xs lg="2">
                                <label>
                                <input type='radio' name = {"tummy"} 
                                value="לא ראיתי" 
                                checked={this.state.selectedOption === 'לא ראיתי'} 
                                onChange={this.handleOptionChange}/>
                                    לא ראיתי
                                </label>
                                </Col>
                        </Row>
                            
                        </Col>
                    </Row>

                <Row style={{height:"50px"}}>
                    <Col>
                    </Col>
                </Row>


                <Row>
                    <Col>
                    
                      פותח וסוגר את עינוי
                    <Row  className="justify-content-md-center">
                                <Col xs lg="2">
                                <label>
                                <input type='radio' name = {"lift11"} 
                                    value="כן ראיתי" 
                                    checked={this.state.selectedOption2 === 'כן ראיתי'} 
                                    onChange={this.handleOptionChange2} />
                                  כן ראיתי
                                  </label>
                                
                                </Col>

                                <Col xs lg="2">
                                <label>
                                <input type='radio' name = {"lift12"} 
                                value="לא ראיתי" 
                                checked={this.state.selectedOption2 === 'לא ראיתי'} 
                                onChange={this.handleOptionChange2}/>
                                    לא ראיתי
                                </label>
                                </Col>
                            </Row> 
                        </Col>
                </Row>

                <Row style={{height:"50px"}}>
                    <Col>
                    </Col>
                </Row>

                <Row>
                    <Col>
                    
                      עושה קולות
                    <Row  className="justify-content-md-center">
                                <Col xs lg="2">
                                <label>
                                <input type='radio' name = {"lift21"} 
                                    value="כן שמעתי" 
                                    checked={this.state.selectedOption3 === 'כן שמעתי'} 
                                    onChange={this.handleOptionChange3} />
                                  כן שמעתי
                                  </label>
                                
                                </Col>

                                <Col xs lg="2">
                                <label>
                                <input type='radio' name = {"lift22"} 
                                value="לא שמעתי" 
                                checked={this.state.selectedOption3 === 'לא שמעתי'} 
                                onChange={this.handleOptionChange3}/>
                                    לא שמעתי
                                </label>
                                </Col>
                            </Row> 
                        </Col>
                </Row>

                

                <Row style={{height:"50px"}}>
                    <Col>
                    </Col>
                </Row>



                <Row>
                    <Col>
                    
                ?כבד
                PARO
                -האם אתה חושב ש
                <Row  className="justify-content-md-center">
                            <Col xs lg="2">
                            <label>
                            <input type='radio' name = {"lift31"} 
                                value="כן, הוא כבד וזה בסדר" 
                                checked={this.state.selectedOption4 === 'כן, הוא כבד וזה בסדר'} 
                                onChange={this.handleOptionChange4} />
                                כן, הוא כבד וזה בסדר
                            </label>
                            
                            </Col>

                            <Col xs lg="2">
                            <label>
                            <input type='radio' name = {"lift32"} 
                            value="כן, הוא כבד וזה מוזר" 
                            checked={this.state.selectedOption4 === 'כן, הוא כבד וזה מוזר'} 
                            onChange={this.handleOptionChange4}/>
                                כן, הוא כבד וזה מוזר
                            </label>
                            </Col>

                            <Col xs lg="2">

                            <label>
                            <input type='radio' name = {"lift43"} 
                            value="לא, הוא לא כבד וזה בסדר" 
                            checked={this.state.selectedOption4 === 'לא, הוא לא כבד וזה בסדר'} 
                            onChange={this.handleOptionChange4} />
                                לא, הוא לא כבד וזה בסדר
                            </label>
                            </Col>

                            <Col xs lg="2">
                            <label>
                            <input type='radio' name = {"lift44"} 
                            value="לא, הוא לא כבד וזה מוזר" 
                            checked={this.state.selectedOption4 === 'לא, הוא לא כבד וזה מוזר'} 
                            onChange={this.handleOptionChange4} />
                                לא, הוא לא כבד וזה מוזר
                            </label>
                            </Col>
                        </Row>


                        
                    </Col>
                </Row>


       
      

                <Row style={{height:"50px"}}>
                    <Col>
                    </Col>
                </Row>


           




                <input type="submit" value="Submit" />

                </form>

                {this.state.saved? <h6>נשמר</h6> : null}

                

                </Col>
            </Row>
            }
            </Col>
        </Row>

    )
  }
}

export default InteractionPageOn;