import React, { Component , useState} from 'react';

import CalibrationPage from './CalibrationPage'
import LongTestPage from './LongTestPage'
import PersonalDetails from './PersonalDetails'


import './ExpPage2.css';



import 'bootstrap/dist/css/bootstrap.min.css';
import {
  Button,
  Container,
  Row,
  Col,
  Navbar,
  Nav
} from "react-bootstrap";


import {
    BrowserRouter as Router,
    Switch,
    Route,
    Link,
    useRouteMatch,
    useParams,

  } from "react-router-dom";

import dateFormat from 'dateformat';
import HappinesPage from './HappinesPage';
import InteractionPageOff from './InteractionPageOff';
import InteractionPageOn from './InteractionPageOn';



const ExpPage2 = (props) => {

    // let { path, url } = useRouteMatch();
    const [startExperiment, setExpirmentStatus] = useState(false);
    const [expirementerDoc, setExpirementerDoc] = useState()
    const [activeTab,changeTab] = useState("start")
    const [menuOrBackButton, setMenu] = useState("menu")

    function initNewDocument(){
        const db = props.db;
        console.log(db)
        db.add({
            calibrationTests: [],
            time:dateFormat(new Date(), "yyyy-mm-dd HH:MM:ss"),
          }).then(docRef => {
              console.log('new doc ref: ' + docRef.id);
              setExpirementerDoc(docRef.id)
              props.sendDocRefToParent(docRef.id)

          }) 


        // console.log("new doc: " + expirementerDoc +  "\n end of doc")
        // console.log("docRef: " + resRef)
    }

    function continuteLastDocument(){
        console.log ("IN USE EFFECT")
        const db = props.db
        db.orderBy('time', 'desc').limit(1).get().
           then((data) =>{
               const docRef = data.docs[0];
            //    const docData = docRef.data()
            //    const docID = docRef.id
               setExpirementerDoc(docRef.id)


           }
           )
        
    }


    

        return (

                <Row>
                    <Col>
                    <h2>
                    דף נבדק 

                </h2>

                    {
                        !startExperiment  ? 
                        <Row>
                            <Col>
                                {/* <Button variant="primary"
                                 onClick = {()=> {
                                    setExpirmentStatus(true);
                                    initNewDocument()
                                    }}> 
                                   התחל ניסוי עבור נסיין חדש
                                </Button>
                                */}

                                <Button  variant="success"
                                    onClick = {()=> {
                                        setExpirmentStatus(true);
                                        continuteLastDocument()
                                        }}
                                    >
                                    המשך
                                </Button>
                            </Col>
                        </Row>

                        : 
                        <Row>
                            <Col>
        
                             
                                {
                                menuOrBackButton === "menu" ? 
                                <Row className="text-light bg-teva-lime py-3">
                                    <Col >
                               
                                    <Navbar   style = {{ marginLeft: "auto", marginRight:"auto", width :"50%"}} >
                                   
                                    
                                    <Nav className="mx-auto justify-content-center">
                                    <Nav.Link
                                        href=""
                                        className="px-6"
                                        active={activeTab === "interactionOff"}
                                        onClick={() => {changeTab("interactionOff"); setMenu("hide")}}

                                        >
                                        off                                   
                                        </Nav.Link>

                                        <Nav.Link
                                        href=""
                                        className="px-6"
                                        active={activeTab === "interactionOn"}
                                        onClick={() => {changeTab("interactionOn"); setMenu("hide")}}

                                        >
                                        on                                   
                                        </Nav.Link>

                                        <Nav.Link
                                            href=""
                                            className="px-6"
                                            onClick={() => {changeTab("happines");setMenu("hide") }}
                                            active={activeTab === "happines"}
                                            >
                                            דירוג אושר
                                        </Nav.Link>

                                        <Nav.Link
                                            href=""
                                            className="px-6"
                                            onClick={() => {changeTab("LongTest");setMenu("hide") }}
                                            active={activeTab === "LongTest"}
                                            >
                                            ניסוי ארוך
                                        </Nav.Link>
                
                                        <Nav.Link
                                            href=""
                                            className="px-6"
                                            onClick={() => {changeTab("Calibration"); setMenu("hide")}}
                                            active={activeTab === "Calibration"}
                                            >
                                            כיול כאב
                                        </Nav.Link>

                                        <Nav.Link
                                            href=""
                                            className="px-6"
                                            active={activeTab === "personalDetails"}
                                            onClick={() => {changeTab("personalDetails"); setMenu("hide")}}

                                            >
                                            טופס פרטי נבדק                                       
                                            </Nav.Link>

                                       


                                    </Nav>
                                    </Navbar>


                                    </Col>
                                </Row>
                           
                                

                                :
                                <Navbar className="text-light bg-teva-lime py-3" >
                                    <Nav className="mx-auto justify-content-center">
                                            <Nav.Link
                                                href=""
                                                className="px-4"
                                                onClick = {()=> setMenu("menu")}
                                                active={activeTab === "LongTest"}
                                                >
                                                חזרה לתפריט
                                            </Nav.Link>
                                        </Nav>
                                </Navbar>

                            }

                                <Row>
                                    <Col>

                                        {
                                            activeTab === "start" ? null :
                                            activeTab === "Calibration" ? <CalibTest docRefID = {expirementerDoc}></CalibTest> :
                                            activeTab === "personalDetails" ? <PersonalDetails  docRefID = {expirementerDoc} ></PersonalDetails> :
                                            activeTab === "happines"    ?   <HappinesPage docRefID = {expirementerDoc}></HappinesPage> : 
                                            activeTab === "interactionOff" ? <InteractionPageOff docRefID = {expirementerDoc}></InteractionPageOff>:
                                            activeTab === "interactionOn" ? <InteractionPageOn  docRefID = {expirementerDoc}></InteractionPageOn> :
                                                                            <LongTest docRefID = {expirementerDoc} />
                                        }
                                    </Col>

                                </Row>



                            
                               </Col>
                        </Row>
                    }
                   
                    </Col>
                </Row>
  

             
        );
    
}

function CalibTest(props) {
    // The <Route> that rendered this component has a
    // path of `/topics/:topicId`. The `:topicId` portion
    // of the URL indicates a placeholder that we can
    // get from `useParams()`.
  
    return (
      <div>

        <h3>
                כיול כאב
            </h3>

            <div>

            <Row>
                    <Col>
                        <CalibrationPage  docRefID = {props.docRefID}/>
                    </Col>
            </Row>
            </div>
      </div>
    );
  }
  
  function LongTest(props) {
    // The <Route> that rendered this component has a
    // path of `/topics/:topicId`. The `:topicId` portion
    // of the URL indicates a placeholder that we can
    // get from `useParams()`.
  
    return (
      <div>

        <h3>
               כאב ארוך
            </h3>

            <div>

            <Row>
                    <Col>
                        <LongTestPage  docRefID = {props.docRefID} />
                    </Col>
            </Row>
            </div>
      </div>
    );
  }
  


export default ExpPage2;