import logo from './logo.svg';
import './App.css';
import React, { Component, useState } from "react";
import ReactGA from 'react-ga';

import {
  BrowserRouter as Router,
  Switch,
  Route,
  Link
} from "react-router-dom";


import firebase from "./services/firebase";

import 'bootstrap/dist/css/bootstrap.min.css';
import {
  Button,
  Container,
  Row,
  Col,
  Navbar,
  Nav
} from "react-bootstrap";
import ManagerPage from './ManagerPage';
import ExpPage2 from './ExpPage2';



class App extends Component {

  constructor(props) {
    super(props);
    this.state = {
      value: '',
      db: firebase.collection("/BGU-lab-dev"),
      activeDocRef: undefined,
      activeTab:"welcome",

    };
    this.changeTab = this.changeTab.bind(this);


    const db = firebase.settings({
        timestampsInSnapshots: true
      });


    const trackingId = "UA-180348020-1"; 
    ReactGA.initialize(trackingId, {
      debug: true
    });
    ReactGA.pageview(window.location.pathname + window.location.search);

  }

  getDocRefFromChild = (docRef) =>{
    console.log(`recibed from child: ${docRef}`)
    this.setState({activeDocRef:docRef})
  }

  componentDidMount() {
    this.unsubscribe = firebase.collection('/BGU-lab-dev').onSnapshot(this.onCollectionUpdate) 

  }

  componentWillUnmount() {
    // we have to unsubscribe when component unmounts, because we don't need to check for updates
    this.unsubscribe()
  }

  changeTab(tab){
    this.setState({ activeTab: tab });

  }


  render(){
    return (
      <div  className="App">

      <Container fluid  className="mx-auto justify-content-center">
        <Row className="text-light bg-teva-lime py-3"> 
          <Col>
            <Navbar   style = {{ marginLeft: "auto", marginRight:"auto", width :"50%"}} >
            <Nav className="mx-auto justify-content-center">
              <Nav.Link
                href=""
                className="px-4"
                onClick={() => this.changeTab("manager")}
                active={this.state.activeTab === "manager"}
                >
                  מנהל ניסוי
              </Nav.Link>
            </Nav>

            <Nav>
              <Nav.Link
                href=""
                className="px-4"
                onClick={() => this.changeTab("experimeneter")}
                active={this.state.activeTab === "experimeneter"}
                >
                  נבדק
              </Nav.Link>
            </Nav>

            </Navbar>

          </Col>
        </Row>

        <Row>
          <Col>

              {
              this.state.activeTab === "welcome" ? <h2> דף כניסה</h2>
                                                  :
            
              this.state.activeTab === "manager" ?
                                        <ManagerPage db = {this.state.db} docRef = {this.state.activeDocRef} />
                                                  :
                                        <ExpPage2  db = {this.state.db} sendDocRefToParent = {this.getDocRefFromChild}/>
              }

          </Col>

        </Row>

      </Container>
      </div>

    );
  }
  
}


export default App;
