import firebase from "./services/firebase";
import React, { Component, useState } from "react";
import Slider, { Range } from 'rc-slider';
import 'rc-slider/assets/index.css';

import 'bootstrap/dist/css/bootstrap.min.css';
import {
  Button,
  Container,
  Row,
  Col
} from "react-bootstrap";


const db = firebase.collection("/BGU-lab-dev");

class InteractionPageOff extends Component{

  constructor(props) {
    super(props);
    this.state ={
      sliderValue: 0,
      saved:false,
      start:false,
      q2:"",
      selectedOption:"",
      bestSpot:"",
      selectedOption2:"",
      q5:"",
      q6:"",
      q7:"",

    }

    this.start = this.start.bind(this);
    this.handleChange = this.handleChange.bind(this);
    this.handleSubmit = this.handleSubmit.bind(this);
    this.handleOptionChange = this.handleOptionChange.bind(this);
    this.handleOptionChange2 = this.handleOptionChange2.bind(this);




    

  }
  componentDidMount() {
    this.unsubscribe = firebase.collection('/slider-data').onSnapshot(this.onCollectionUpdate) 
    this.setState({sliderValue:0})

  }

  componentWillUnmount() {
    // we have to unsubscribe when component unmounts, because we don't need to check for updates
    this.unsubscribe()
  }

  
  onCollectionUpdate = (querySnapshot) => {
    // we have to update the state
    let value = 0;
    querySnapshot.forEach((document) => {
      const doc= document.data();
      console.log(doc)
      value = doc.value
    })
    this.setState({value:value, sliderValue:value})

  }



  getData() {
    let value;
    db.get().then(function(snapshot) {
      snapshot.forEach( function(childSnapshot){
        db.doc(childSnapshot.id).get().
        then( docSnapshot => {
          [docSnapshot].forEach( doc => {
            const data = doc.data();
            console.log(doc.id, data);
            console.log(`sliderID: ${data.id}, value: ${data.value}`)
            value =  data.value;
          })

        })
        //console.log(`id: ${childSnapshot.id}, data: ${childSnapshot.data}`)
      })
    })
    console.log(value)
    return value
  }






  update( input) {
    db.get().then(function(snapshot) {
      snapshot.forEach( function(childSnapshot){
        db.doc(childSnapshot.id).update({value:input})

      })
    })
  }




 
    


  onSliderChange = value => {
    this.setState(
      {
        sliderValue:value
      },
      () => {
        console.log(this.state.sliderValue);
      }
    );
  };


  
  handleChange(event) {
    const value = event.target.value;
    const name = event.target.name
    let update =  {}
    update[name] = value
    this.setState(update);
  }

  async handleSubmit(event) {
    event.preventDefault();
    db.orderBy('time', 'desc').limit(1).get().
            then((data) =>{
              const docRef = data.docs[0];
              const docData = docRef.data()
              const docID = docRef.id
              const dataUpdate = {
                "האם נעים לגעת בו": this.state.sliderValue,
                 "מה ההבדל בין הסנפירים ":this.state.q2,
                 "איזה איזור יותר נעים לליטוף": this.state.selectedOption,
                 "בחר איזור הכי נעים לליטוף אחר":this.state.bestSpot,
                 "כבד? (מוזמן להרים אותו) PARO -האם אתה חושב ש": this.state.selectedOption2,
                 "?וענה - אם היה צריך למצוא שם חדש עבורו, איך היית קורא לו PARO אנא הסתכל על":this.state.q5,
                 "?PARO מה לדעתך הגיל של":this.state.q6,
                 "?PARO מה אתה חושב על": this.state.q7,
                }

              const docRefUpdate = db.doc(docRef.id);
            //   docRefUpdate.update(dataUpdate)
              docRefUpdate.update(dataUpdate)
              console.log('updating doc with: ' +JSON.stringify(dataUpdate) )
              this.setState({saved:true})
            })
 

  }

  
handleOptionChange(e) {
    this.setState({
      selectedOption: e.target.value
    });
  }

  handleOptionChange2(e) {
    this.setState({
      selectedOption2: e.target.value
    });
  }
  
  start(event) {
    event.preventDefault();
    //this.setState({sliderValue:0})
    this.setState({sliderValue:0, start:true})

  }

  render(){
    let sliderHeight = 200;
    return (
        <Row>

          <Col>
          {
          !this.state.start ?  
                    <Button onClick ={this.start}>
                    התחל
                  </Button>
          :


        <Row>
            <Col>

           
            <Row>
                <Col>
                    <h3>: PARO ענו על השאלות הבאות לגבי </h3>
                </Col>
            </Row>

        
            
            <form onSubmit={this.handleSubmit}>

                <Row>
                    <Col>
                        <Row>
                            <Col>
                            ?וענו על השאלה - האם נעים לגעת בו             
                                PARO
                                אנא לטפו את
                            </Col>
                        </Row>



            
                    <Row lassName="justify-content-md-center">
                    <Col xs lg={2}>
                        </Col>

                        <Col xs lg={2}>
                        נעים מאוד
                        </Col>
                    

                        <Col xs lg={4}>
                        
                                <Slider
                                    min={0}
                                    max={10}
                                    defaultValue={0}
                                    value={this.state.sliderValue}
                                    onChange={this.onSliderChange}
                                    step ={0.1}
                                    reverse = {true}
                                    railStyle={{ backgroundColor: 'grey', height: 10 }}
                                    trackStyle={{ backgroundColor: 'lightGreen', height: 10 }}
                                    handleStyle={{
                                        borderColor: 'grey',
                                        height: 10,
                                        width: 1,
                                        marginLeft: -14,
                                        marginTop: 0,
                                        backgroundColor: 'white',
                                    }} />
                        
                        </Col>

                        <Col xs lg={2}>
                        לא נעים בכלל
                        </Col>
                    </Row>
                    </Col>
                </Row>

                <Row style={{height:"50px"}}>
                    <Col>
                    </Col>
                </Row>
   
                <Row>
                    <Col>
                    <label>
                    
                ?לאחוריים
                PARO
                מה ההבדל בין הסנפירים הקדמיים של
                <Row>
                            <Col>
                            <textarea style={{width:"100%"}} name = {"q2"} value={this.state.q2} onChange={this.handleChange} />

                            </Col>
                        </Row>
                    </label>


                    
                    </Col>
                </Row>

                <Row style={{height:"50px"}}>
                    <Col>
                    </Col>
                </Row>
   

                <Row>
                    <Col>
                    
                ?PARO
                -וענו - איזה איזור יותר נעים לליטוף ב
                PARO
                אנא לטפו את
                <Row  className="justify-content-md-center">
                            <Col xs lg="2">
                            <label>
                            <input type='radio' name = {"head"} 
                                value="הראש" 
                                checked={this.state.selectedOption === 'הראש'} 
                                onChange={this.handleOptionChange} />
                                הראש
                            </label>
                            
                            </Col>

                            <Col xs lg="2">
                            <label>
                            <input type='radio' name = {"tummy"} 
                            value="הבטן" 
                            checked={this.state.selectedOption === 'הבטן'} 
                            onChange={this.handleOptionChange}/>
                                הבטן
                            </label>
                            </Col>

                            <Col xs lg="2">

                            <label>
                            <input type='radio' name = {"other"} 
                            value="אחר" 
                            checked={this.state.selectedOption === 'אחר'} 
                            onChange={this.handleOptionChange} />
                                אחר
                            </label>
                            </Col>
                        </Row>

                        <Row style={{height:"50px"}}>
                            <Col>
                            </Col>
                        </Row>

                        {
                            this.state.selectedOption === 'אחר' ?
                            <div>
                              ?איזה איזור הכי נעים לליטוף
                                <Row>
                                    <Col>

                                    <textarea  style={{width:"50%"}}  name = {"bestSpot"} value={this.state.bestSpot} onChange={this.handleChange} />

                                    </Col>
                                </Row>   
                            </div>




                            :null
                        }

                        
                    </Col>
                </Row>

                <Row style={{height:"50px"}}>
                    <Col>
                    </Col>
                </Row>

                {/* T2.1*/}

                <Row>
                    <Col>
                    
                כבד? (מוזמן להרים אותו)
                PARO
                -האם אתה חושב ש
                <Row  className="justify-content-md-center">
                            <Col xs lg="2">
                            <label>
                            <input type='radio' name = {"lift1"} 
                                value="כן, הוא כבד וזה בסדר" 
                                checked={this.state.selectedOption2 === 'כן, הוא כבד וזה בסדר'} 
                                onChange={this.handleOptionChange2} />
                                כן, הוא כבד וזה בסדר
                            </label>
                            
                            </Col>

                            <Col xs lg="2">
                            <label>
                            <input type='radio' name = {"lift2"} 
                            value="כן, הוא כבד וזה מוזר" 
                            checked={this.state.selectedOption2 === 'כן, הוא כבד וזה מוזר'} 
                            onChange={this.handleOptionChange2}/>
                                כן, הוא כבד וזה מוזר
                            </label>
                            </Col>

                            <Col xs lg="2">

                            <label>
                            <input type='radio' name = {"lift3"} 
                            value="לא, הוא לא כבד וזה בסדר" 
                            checked={this.state.selectedOption2 === 'לא, הוא לא כבד וזה בסדר'} 
                            onChange={this.handleOptionChange2} />
                                לא, הוא לא כבד וזה בסדר
                            </label>
                            </Col>

                            <Col xs lg="2">
                            <label>
                            <input type='radio' name = {"lift4"} 
                            value="לא, הוא לא כבד וזה מוזר" 
                            checked={this.state.selectedOption2 === 'לא, הוא לא כבד וזה מוזר'} 
                            onChange={this.handleOptionChange2} />
                                לא, הוא לא כבד וזה מוזר
                            </label>
                            </Col>
                        </Row>


                        
                    </Col>
                </Row>

                <Row style={{height:"50px"}}>
                    <Col>
                    </Col>
                </Row>

                    <label>

                    ?וענה - אם היה צריך למצוא שם חדש עבורו, איך היית קורא לו
                    PARO
                    אנא הסתכל על
                    <Row>
                        <Col>
                         <textarea   style={{width:"100%"}} name = {"q5"} value={this.state.q5} onChange={this.handleChange} />
                        </Col>
                      </Row>
                    </label>



      

                <Row style={{height:"50px"}}>
                    <Col>
                    </Col>
                </Row>


                ?PARO
                   מה לדעתך הגיל של 

              <Row>
                  <Col>
                  <textarea  style={{width:"50%"}}  name = {"q6"} value={this.state.q6} onChange={this.handleChange} />

                  </Col>
              </Row>




                <Row style={{height:"50px"}}>
                    <Col>
                    </Col>
                </Row>

                <Row>
                    <Col>
                   ?PARO
                   מה אתה חושב על  
                    <Row>
                            <Col>
                            <textarea   style={{width:"50%"}}  name = {"q7"} value={this.state.q7} onChange={this.handleChange} />

                            </Col>
                        </Row>
                    </Col>
                </Row>



                <input type="submit" value="Submit" />

                </form>

                {this.state.saved? <h6>נשמר</h6> : null}

                

                </Col>
            </Row>
            }
            </Col>
        </Row>

    )
  }
}

export default InteractionPageOff;