import React, { useState , useEffect} from 'react';
import 'bootstrap/dist/css/bootstrap.min.css';
import {
  Button,
  Container,
  Row,
  Col,
  ListGroup,

} from "react-bootstrap";
import firebase from "./services/firebase";

import dateFormat from 'dateformat';
import { CSVLink, CSVDownload } from "react-csv";
import {Parser} from "json2csv"

import Workbook from 'react-excel-workbook'




 const ManagerPage = (props) => {

        const [experimemtDocID, setExpirementerDoc] = useState();
        const [experimemterName, setExpirementerName] = useState("");
        const [experimemtTime, setExpirementTime] = useState("");
        const [experimentUpdatingData, setExpData] = useState()
        const [dataAsLsit, setDataAsList] = useState([])
        const [loaded, setAsLoaded] = useState(false)
        const [dataRenderedasList, setRenderData] = useState([])
        const [newDocAlarm, setNewDocAlarm] = useState(true)
        const [justCreatedDocID, setJustCreatedDocId] = useState("");
        const [comment,handleChangeComment] = useState();
        const [allComments, setAllComments] =useState({});
        const [download,setDownload] = useState(    [
            ["notSaved"],
            ["notSaved"], ]    )

        const [okToDownload, setOkToDownload] = useState(false)
        const [okToDownloadAll, setOkToDownloadAll] = useState(false)
        // const [downloadAll,setDownloadAll] = useState(    [
        //     ["notSaved"],
        //     ["notSaved"], ]    )
            const [downloadAll,setDownloadAll] = useState([])
        const[workBook,setWorkBook] = useState(<h6>wait</h6>)
    


    useEffect(() => { 
        const db = props.db
        db.orderBy('time', 'desc').limit(1).get().
           then((data) =>{
               const docRef = data.docs[0];
               const docData = docRef.data()
               const docID = docRef.id
               setExpirementerDoc(docID)
               setExpData(data)
               const time = docData.time.split(" ")[1].split(":")
               setExpirementTime(time[0] + ":" + time[1])
               setAsLoaded(true)
               onCollectionUpdate()

               renderData3()

           } 
           )


        //remember to unsubscribe from your realtime listener on unmount or you will create a memory leak
        return () => undefined
    }, []);


    function getExperimenterName(){
        const db = props.db
        db.orderBy('time', 'desc').limit(1).get().
           then((data) =>{
               const docRef = data.docs[0];
               const docData = docRef.data()
               const docID = docRef.id
               const firstName = docData.firstName
               const lastName = docData.lastName
               console.log("name: "+ firstName + " " + lastName)
               //

               setExpirementerName(firstName + " " + lastName)


           } 
           )

    }


     function getLateseDocID(){
        const db = props.db
         db.orderBy('time', 'desc').limit(1).get().
            then((data) =>{
                const docRef = data.docs[0];
                const docData = docRef.data()
                const docID = docRef.id
                setExpirementerDoc(docID)
                const time = docData.time.split(" ")[1].split(":")

                setExpirementTime(time[0] + ":" + time[1])
                setAsLoaded(true)
                getExperimenterName()

                onCollectionUpdate()
                renderData3()


            } 
            )

    }
   

     function onCollectionUpdate() {
         console.log("update: " +experimemtDocID)
         if(experimemtDocID != undefined){
            const doc = props.db.doc(experimemtDocID)

            const observer = doc.onSnapshot(docSnapshot => {
                const data = docSnapshot.data();
                // console.log(`Received doc snapshot:${docSnapshot.id} ${data}`);
               // console.log(data)
                setExpData(data)
                renderData3()
                getExperimenterName()
                // ...
              }, err => {
                console.log(`Encountered error: ${err}`);
              });

         }
        

      }


    function initNewDocument(){
        setNewDocAlarm(true);
        const db = props.db;
        console.log(db)
        db.add({
            calibrationTests: [],
            time:dateFormat(new Date(), "yyyy-mm-dd HH:MM:ss"),
          }).then(docRef => {
              console.log('*** STARTED NEW DOC: ' + docRef.id);
              setJustCreatedDocId(docRef.id)
              

            //   setExpirementerDoc(docRef.id)

          }).then(()=>getLateseDocID() ) 


    }



    function continuteLastDocument(){
        const db = props.db
        db.orderBy('time', 'desc').limit(1).get().
           then((data) =>{
               const docRef = data.docs[0];
            //    const docData = docRef.data()
            //    const docID = docRef.id
            //    setExpirementerDoc(docRef.id)
            console.log("*** CONTINUE last doc: " +docRef.id )
            setNewDocAlarm(false)
            renderData3()

            getExperimenterName()



           }
           )
        
    }

    function handleChange(evt) {
        const value = evt.target.value;
        const name = evt.target.name
        let allComs = allComments
        allComs[name] = value
        setAllComments(allComs)
        handleChangeComment({
          ...comment,
          [evt.target.name]: value
        });
        const db = props.db
        db.orderBy('time', 'desc').limit(1).get().
            then((data) =>{
                const docRef = data.docs[0];
                const docRefUpdate = db.doc(docRef.id);
                const docData = docRef.data()

                let calibTest = docData["calibrationTests"]
                console.log("calbs: " +JSON.stringify(calibTest))
                 calibTest[name] = {...calibTest[name], text:value  }
                // docRefUpdate.update({calibrationTests: calibTest})


            })
      }



      function saveDataToCloud() {
          const allComs = allComments
          const db = props.db
          console.log(`saving all comments:` + JSON.stringify(allComments))
          db.orderBy('time', 'desc').limit(1).get().
            then((data) =>{
                const docRef = data.docs[0];
                const docRefUpdate = db.doc(docRef.id);
                const docData = docRef.data()

                let calibTest = docData["calibrationTests"]
                let i=0;
                calibTest.forEach( (item) => {
                    if (allComments[i]) {
                        item.text = allComs[i]
                    }
                    i++;
                })
                console.log("updated calbs: " +JSON.stringify(calibTest))

                docRefUpdate.update({calibrationTests: calibTest})
                downloadFile()
                setOkToDownload(true)


            })
          
      }


    function renderData3(){
        if(experimentUpdatingData){


            const doc = props.db.doc(experimemtDocID)

            const observer = doc.onSnapshot(docSnapshot => {
                const data = docSnapshot.data();
                console.log(`*** RENDERING list of :${docSnapshot.id}`);
               // console.log(data)
                let list = []
                let calibTest = data["calibrationTests"]
                let name = 0

                calibTest.forEach( (item) => {
                    // console.log(`${item.testNum} : ${item.value}`)
                    let text = item?.text;
                    let time = item.time
                    console.log('text :' +text)
                    list.push(
                        <Row>
                            <Col>
                                
                                <form>
                                {/* <li key={id} style={{ listStyleType: "none" }}>
                                {item.testNum} : {item.value}
                                
                                </li> */}

                                    <label>
                                    <textarea
                                    name = {name}
                                    type="text"
                                    value={comment}
                                    onChange={handleChange}
                                    style= {{
                                        width: "600px",
                                        height: "50px"
                                    }}
                                  >
                                       {text}
                                  </textarea>
                                      {item.testNum} - {item.value} , {time}

                                    </label>
                                </form>
                         </Col>
                        </Row>
                    )

                    name++;

                }
                
                )
                console.log(docSnapshot.id + " *,* " + justCreatedDocID)

                if (list.length > 0)  {
                    setRenderData(list);
                    if(docSnapshot.id === justCreatedDocID){
                        console.log("***renering the newest list")

                    }
                }
                else{
                    setRenderData(<h6>פרטי ניסוי יועדכנו כאשר הניסוי יחל</h6>)
                }
                

                // ...
              }, err => {
                console.log(`Encountered error: ${err}`);
              });
            

        }
  
      }

        async function downloadLastWeek(){
            const db = props.db
            const alldocs = await db.orderBy('time', 'desc').limit(100).get()
            let count = 0
            let fields = ["calibrationTests", "time", "firstName","lastName"];
            let i=0;
            let workBookList = []
            let allWorkBookSheets = []
            let currData = {}

            await alldocs.forEach( (doc) =>{
                let docData = doc.data()
                let docTime = docData.time;
                let currentTime = new Date();
                currData = {}
                workBookList=[]

                let twoWeeksAgo = currentTime.setDate(currentTime.getDate()-14);
                if(Date.parse(docTime) > twoWeeksAgo){
                    if ('firstName' in docData){
                        currData["firstName"] = docData.firstName
                        workBookList.push(<Workbook.Column label={'firstName'} value={'firstName'}/>)

                    }

                    if ('lastName' in docData){
                        currData["lastName"] = docData.lastName
                        workBookList.push(<Workbook.Column label={'lastName'} value={'lastName'}/>)

                    }

                    if ('time' in docData){
                        currData["time"] = docData.time
                        workBookList.push(<Workbook.Column label={'time'} value={'time'}/>)

                    }

                    for (let [k,v] of Object.entries(docData)){
                        
                        if (k!=='calibrationTests' && k!== 'firstName' && k!=='lastName' && k!=='time'){
                            currData[k] = v
                            workBookList.push(<Workbook.Column label={k} value={k}/>)
                        }
                    }
                    console.log('currData : ' + JSON.stringify(currData))
                    count++
                    let calibTest = docData["calibrationTests"]
                    i=0;
                    if (calibTest ) {
                        
                        calibTest.forEach( (item) => {
                            let testNum = item.testNum + '  [' + i.toString() + ']'
                            let val = item.value
                            if (allComments[i]) {
                                val =  val+  " , "  +  allComments[i] 
                            }
                            currData[testNum] = val
                            workBookList.push(<Workbook.Column label={testNum} value={testNum}/>)
                            i++;
                        })

                    }
                    let currSheet = (
                        <Workbook.Sheet data={[currData]} name={`${currData.firstName} ${currData.lastName} ${count}`} >
                            {workBookList}
                        </Workbook.Sheet>


                    )
                    allWorkBookSheets.push(currSheet)
                
                }
            } )
            const parser = new Parser({
                fields,
                unwind: fields,
                encoding: "uft8"
            });
            
            // setWorkBook(workBookList)
            // setDownloadAll([currData])

        //  setWorkBook(
        //         <Workbook filename="example.xlsx" element={<button className="btn btn-lg btn-primary">Try me!</button>}>
        //         {allWorkBookSheets}
        //     </Workbook>  
        //     )
            let finalComponent = (
                <Workbook filename="last2weeks.xlsx" element={<button className="btn btn-lg btn-primary">לחצ/י להודרת השבועיים האחרונים</button>}>
                {allWorkBookSheets}
            </Workbook>
            )
            setWorkBook(finalComponent)
            setOkToDownloadAll(true)


    
            
            console.log('workbook: ' + JSON.stringify( finalComponent))
      
        }

        async function downloadFile(){
        var obj = allComments
        // var data = "text/csv;charset=utf-8," + encodeURIComponent(JSON.stringify(obj));
        const db = props.db
        const alldocs = await db.orderBy('time', 'desc').limit(1).get()
        const latestDoc = alldocs.docs[0]
        let docData = latestDoc.data()
        console.log(`exporting: ${JSON.stringify(docData)}`)

        
        let fields = ["calibrationTests", "time", "firstName","lastName"];
        let calibTest = docData["calibrationTests"]
        let i=0;
        let parsedData = {}

        calibTest.forEach( (item) => {
            let testNum = item.testNum + '  [' + i.toString() + ']'
            let val = item.value
            fields.push(testNum)
            docData[testNum] = val
            parsedData[testNum] = val
            if (allComments[i]) {
                val =  val+  " , "  +  allComments[i] 
            }
            docData[testNum] = val

            i++;
        })
        
        const parser = new Parser({
            fields,
            unwind: fields,
            encoding: "uft8"
        });
        delete docData['calibrationTests'];
        
        const csv = parser.parse(docData);
        setDownload(csv)
        console.log('output',csv);

      }

      


        return (
            <div>
                <h3>דף מנהל</h3>
                <h6>שם הנסיין: {experimemterName} </h6>
                <h6>זמן תחילת הניסוי: {experimemtTime} </h6>

            
                <Row>
                        <Col>
                            <Button variant="primary"
                                onClick = {()=> {
                                initNewDocument();
                                }}> 
                                התחל ניסוי עבור נסיין חדש
                            </Button>
                            | 

                            {/* <Button variant="success"
                                onClick={getLateseDocID}>
                                הצג פרטי ניסוי נוכחי
                            </Button>

                            | */}

                            <Button  variant="success"
                                    // disabled={!newDocAlarm}

                                onClick = {()=> {
                                    continuteLastDocument();
                                    }}
                                >
                                המשך ניסוי אחרון
                            </Button>
                        </Col>
                    </Row>

                    {
                        newDocAlarm ?
                        <h6 style ={{color:"red"}}>
                            יש ללחוץ על  "המשך ניסוי אחרון" כדי לראות פרטי ניסוי עדכני
                        </h6>:
                        null
                    }



                <div>
                    <ul tyle={{ listStyleType: "none" }}>

                        {
                     

                                dataRenderedasList
                                
                
                        }
                    

                    </ul>

                    {
                     
                    }

                </div>
                <Button onClick={saveDataToCloud}>
                    שמור פרטי ניסוי נוכחי
                </Button>
               


                  

          
               {
                   
                   okToDownload?  
                       <CSVLink data={download} 
                   filename={`${experimemterName}.csv`}
                    className="btn btn-primary"
                    >
                        הורד פרטי ניסוי
                    </CSVLink>
                     :
                     <Button disabled ={!okToDownload}>
                         הורדת ניסוי נוכחי - יש לשמור פרטי ניסוי קודם
                     </Button>

               }

               <Row>
                   <Col>
                   {
                    okToDownloadAll ? 
                    <div>
                        {workBook}
                    </div>
                    :

                    <Button onClick={downloadLastWeek}>
                         טעינת פרטי ניסויים מהשבועיים האחרונים 
                    </Button>
                }
                   </Col>
               </Row>
               

               



            </div>
        );
}

export default ManagerPage;