import firebase from "./services/firebase";
import React, { Component, useState } from "react";
import Slider, { Range } from 'rc-slider';
import 'rc-slider/assets/index.css';

import 'bootstrap/dist/css/bootstrap.min.css';
import {
  Button,
  Container,
  Row,
  Col
} from "react-bootstrap";


const db = firebase.collection("/BGU-lab-dev");

class PersonalDetails extends Component{

  constructor(props) {
    super(props);
    this.state ={
      testCouner: 0,
      sliderValue: 0,
      start:false,
      lastDocID:"",
      firstName:"",
      lastName:"",
      saved:false,

    }


    this.handleChange = this.handleChange.bind(this);
    this.handleSubmit = this.handleSubmit.bind(this);


    

  }
  componentDidMount() {
    this.unsubscribe = firebase.collection('/slider-data').onSnapshot(this.onCollectionUpdate) 
    this.setState({sliderValue:0})

  }

  componentWillUnmount() {
    // we have to unsubscribe when component unmounts, because we don't need to check for updates
    this.unsubscribe()
  }

  
  onCollectionUpdate = (querySnapshot) => {
    // we have to update the state
    let value = 0;
    querySnapshot.forEach((document) => {
      const doc= document.data();
      console.log(doc)
      value = doc.value
    })
    this.setState({value:value, sliderValue:value})

  }



  getData() {
    let value;
    db.get().then(function(snapshot) {
      snapshot.forEach( function(childSnapshot){
        db.doc(childSnapshot.id).get().
        then( docSnapshot => {
          [docSnapshot].forEach( doc => {
            const data = doc.data();
            console.log(doc.id, data);
            console.log(`sliderID: ${data.id}, value: ${data.value}`)
            value =  data.value;
          })

        })
        //console.log(`id: ${childSnapshot.id}, data: ${childSnapshot.data}`)
      })
    })
    console.log(value)
    return value
  }






  update( input) {
    db.get().then(function(snapshot) {
      snapshot.forEach( function(childSnapshot){
        db.doc(childSnapshot.id).update({value:input})

      })
    })
  }




 
    


  onSliderChange = value => {
    this.setState(
      {
        sliderValue:value
      },
      () => {
        console.log(this.state.sliderValue);
      }
    );
  };


  
  handleChange(event) {
    const value = event.target.value;
    const name = event.target.name
    let update =  {}
    update[name] = value
    this.setState(update);
  }

  async handleSubmit(event) {
    event.preventDefault();
    db.orderBy('time', 'desc').limit(1).get().
            then((data) =>{
              const docRef = data.docs[0];
              const docData = docRef.data()
              const docID = docRef.id
              const dataUpdate = {firstName: this.state.firstName, lastName:this.state.lastName}
              const docRefUpdate = db.doc(docRef.id);
              docRefUpdate.update(dataUpdate)
              console.log('updating doc with: ' +JSON.stringify(dataUpdate) )
              this.setState({saved:true})
            })
 

  }
  
  render(){
    let sliderHeight = 200;
    return (
        <Row>
          <Col>
          <form onSubmit={this.handleSubmit}>

            <Row>
                <Col>

                <label>
                <input name ={"firstName"} value={this.state.firstName} onChange={this.handleChange} />
                :שם פרטי
                </label>

                </Col>
            </Row>

            <Row>
                <Col>
                <label>
                <input name = {"lastName"} value={this.state.lastName} onChange={this.handleChange} />
                :שם משפחה
                </label>


                
                </Col>
            </Row>
            <input type="submit" value="Submit" />

            </form>

            {this.state.saved? <h6>נשמר</h6> : null}

                


            </Col>
        </Row>

    )
  }
}

export default PersonalDetails;